"use client"

import { useEffect, useState, useCallback, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { 
  ChevronLeft, Send, User, MessageSquare, 
  LogOut, Loader2, Sparkles, Hash, Plus, Search 
} from "lucide-react"

import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'

export default function RealTimeChatPage() {
  const router = useRouter()
  
  // 주신 코드의 전역 변수 대응 상태
  const [userId, setUserId] = useState("")
  const [userName, setUserName] = useState("")
  const [activeTab, setActiveTab] = useState("GROUP") // currentTab
  const [rooms, setRooms] = useState<any[]>([])
  const [searchQuery, setSearchQuery] = useState("")
  const [currentRoom, setCurrentRoom] = useState<any>(null) // currentRoomId 포함 객체
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  const [isLoadingHistory, setIsLoadingHistory] = useState(false)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)
  const subscriptionRef = useRef<any>(null)

  // 주신 코드의 apiBaseUrl 설정 반영
  const API_BASE = 'http://sparta-nextme.xyz:18080'

  // 1. 방 목록 로드 (loadRoomListByTab 로직)
  const loadRooms = useCallback(async (type: string) => {
    const token = localStorage.getItem("accessToken")
    if (!token) return
    try {
      // 주신 코드의 경로: /v1/chats/room?roomType=
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=${type}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      // 주신 코드의 데이터 추출 방식: data.rooms || data
      const roomList = data.rooms || data.result || (Array.isArray(data) ? data : [])
      setRooms(roomList)
    } catch (e) { 
      setRooms([]) 
    }
  }, [])

  // 2. 메시지 로드 (loadMessages 로직)
  const loadChatHistory = async (roomId: string) => {
    const token = localStorage.getItem("accessToken")
    setIsLoadingHistory(true)
    try {
      // 주신 코드 경로: /v1/chats/message/${roomId}?size=20
      const res = await fetch(`${API_BASE}/v1/chats/message/${roomId}?size=50`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const rawMessages = Array.isArray(data) ? data : (data.messages || data.result || [])
      
      // 주신 코드 로직: 최신이 아래로 오게 reverse
      const history = [...rawMessages].reverse()
      
      // 주신 코드의 addSystemMessage('방에 입장했습니다', 'join') 반영
      const systemMsg = {
        senderName: "시스템",
        content: "방에 입장했습니다",
        isSystem: true,
        createdAt: new Date().toISOString()
      }
      
      setMessages([...history, systemMsg])
    } catch (e) { 
      setMessages([]) 
    } finally { 
      setIsLoadingHistory(false)
      scrollToBottom()
    }
  }

  // 3. 방 나가기 (leaveRoom 로직 완벽 구현)
  const handleLeaveRoom = async () => {
    if (!currentRoom) return
    if (!confirm('이 채팅방을 나가시겠습니까?')) return

    const token = localStorage.getItem("accessToken")
    const roomId = currentRoom.chatRoomId || currentRoom.id
    
    try {
      // 주신 코드 경로: /v1/chats/room/${currentRoomId}/leave
      const res = await fetch(`${API_BASE}/v1/chats/room/${roomId}/leave`, {
        method: "POST",
        headers: { 
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json" 
        }
        // 주신 백엔드 컨트롤러가 Principal을 사용하므로 Body는 생략 가능하지만, 
        // JS 코드에 적힌대로 userId를 포함할 수도 있습니다.
      })

      if (res.ok) {
        // 주신 코드 순서: 구독해제 -> UI 초기화 -> 목록 새로고침
        if (subscriptionRef.current) {
          subscriptionRef.current.unsubscribe()
          subscriptionRef.current = null
        }
        
        setCurrentRoom(null)
        setMessages([])
        
        // 목록 새로고침 (이게 빠지면 새로고침 시 방이 다시 보임)
        await loadRooms(activeTab)
        
        alert('채팅방에서 나갔습니다')
      }
    } catch (e) {
      alert('방 나가기 실패')
    }
  }

  // 4. WebSocket 연결 (connect 로직)
  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    if (!token) return router.push("/login")
    
    setUserId(localStorage.getItem("userId") || "")
    setUserName(localStorage.getItem("name") || "사용자")

    const client = new Client({
      // 주신 경로: /ws/chat
      brokerURL: `ws://sparta-nextme.xyz:18080/ws/chat`,
      connectHeaders: { Authorization: `Bearer ${token}` },
      webSocketFactory: () => new SockJS(`${API_BASE}/ws/chat`),
      onConnect: () => {
        setIsConnected(true)
        loadRooms(activeTab) // 연결 후 목록 로드
      },
      onDisconnect: () => setIsConnected(false)
    })

    client.activate()
    stompClient.current = client
    return () => { if (stompClient.current) stompClient.current.deactivate() }
  }, [activeTab, loadRooms, router])

  // 5. 방 선택 (selectRoom & subscribeToRoom 로직)
  const selectRoom = (room: any) => {
    const roomId = room.chatRoomId || room.id
    if (currentRoom?.chatRoomId === roomId || currentRoom?.id === roomId) return
    
    // 기존 구독 해제
    if (subscriptionRef.current) subscriptionRef.current.unsubscribe()

    setCurrentRoom(room)
    loadChatHistory(roomId)

    if (stompClient.current?.connected) {
      // 주신 목적지: /topic/chat.room.
      const destination = `/topic/chat.room.${roomId}`
      subscriptionRef.current = stompClient.current.subscribe(destination, (msg) => {
        const received = JSON.parse(msg.body)
        setMessages((prev) => [...prev, received])
        scrollToBottom()
      })
    }
  }

  // 6. 메시지 전송 (sendMessage 로직)
  const handleSendMessage = (e?: React.FormEvent) => {
    if (e) e.preventDefault()
    if (!inputValue.trim() || !stompClient.current?.connected || !currentRoom) return

    const roomId = currentRoom.chatRoomId || currentRoom.id
    const roomType = currentRoom.roomType || currentRoom.type || activeTab

    // 주신 코드의 전송 객체 구조 반영
    const message = {
      content: inputValue,
      roomType: roomType
    }

    stompClient.current.publish({
      // 주신 목적지: /app/chat.send/
      destination: `/app/chat.send/${roomId}`,
      body: JSON.stringify(message),
    })
    setInputValue("")
    scrollToBottom()
  }

  const scrollToBottom = () => {
    setTimeout(() => {
      if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }, 100)
  }

  return (
    <div className="h-screen bg-background text-foreground flex flex-col overflow-hidden">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm h-16 shrink-0 z-50">
        <div className="container mx-auto px-4 h-full flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" onClick={() => router.push("/dashboard")}>
              <ChevronLeft className="h-5 w-5" />
            </Button>
            <Link href="/" className="flex items-center gap-2">
               <span className="text-xl font-black text-primary tracking-tighter">NextMe Chat</span>
            </Link>
          </div>
          <div className="flex items-center gap-4">
             <div className="flex items-center gap-2 px-3 py-1 bg-secondary/30 rounded-full border border-border/50">
                <div className={`h-2 w-2 rounded-full ${isConnected ? 'bg-green-500 animate-pulse' : 'bg-slate-500'}`} />
                <span className="text-[10px] font-bold text-muted-foreground uppercase">{isConnected ? "Online" : "Offline"}</span>
             </div>
             <div className="text-sm font-bold">{userName}</div>
          </div>
        </div>
      </header>

      <div className="flex-1 flex container mx-auto px-0 md:px-4 overflow-hidden h-[calc(100vh-64px)]">
        <aside className="w-80 border-r border-border hidden md:flex flex-col bg-card/20 backdrop-blur-md h-full">
          <div className="p-6 shrink-0 space-y-4">
            <div className="flex items-center justify-between">
              <h2 className="text-xs font-black uppercase tracking-widest text-muted-foreground/60">Rooms</h2>
              <Plus className="h-4 w-4 cursor-pointer hover:text-primary" />
            </div>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-3.5 w-3.5 text-muted-foreground" />
              <input 
                placeholder="검색..." 
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-full pl-9 h-9 text-xs bg-secondary/30 border-none rounded-md focus:ring-1 focus:ring-primary/30 outline-none p-2" 
              />
            </div>
            <Tabs value={activeTab} onValueChange={(v) => { setActiveTab(v); setCurrentRoom(null); }} className="w-full">
              <TabsList className="grid grid-cols-2 w-full">
                <TabsTrigger value="GROUP" className="text-xs font-bold">그룹</TabsTrigger>
                <TabsTrigger value="DIRECT" className="text-xs font-bold">1:1</TabsTrigger>
              </TabsList>
            </Tabs>
          </div>

          <div className="flex-1 overflow-y-auto px-4 space-y-2 pb-6 custom-scrollbar">
            {rooms.filter(r => (r.title || r.name || "").includes(searchQuery)).map((room: any) => (
              <button
                key={room.chatRoomId || room.id}
                onClick={() => selectRoom(room)}
                className={`w-full p-4 rounded-xl flex flex-col items-start gap-1 transition-all border ${
                  (currentRoom?.chatRoomId || currentRoom?.id) === (room.chatRoomId || room.id) 
                  ? 'bg-primary/10 border-primary/40 shadow-sm' 
                  : 'bg-transparent border-transparent hover:bg-secondary/30'
                }`}
              >
                <span className="font-bold text-sm truncate">{room.title || room.name || '채팅방'}</span>
                <p className="text-[10px] opacity-60 truncate">{room.lastMessage || "메시지가 없습니다"}</p>
              </button>
            ))}
          </div>
        </aside>

        <main className="flex-1 flex flex-col bg-background/50 h-full overflow-hidden">
          {currentRoom ? (
            <>
              <div className="h-16 px-6 border-b border-border flex items-center justify-between bg-card/30 shrink-0">
                <div className="flex items-center gap-3">
                  <h2 className="font-bold text-sm tracking-tight">{currentRoom.title || currentRoom.name}</h2>
                </div>
                <Button variant="ghost" size="sm" onClick={handleLeaveRoom} className="text-xs font-bold text-red-500 hover:bg-red-50">
                  <LogOut className="h-4 w-4 mr-2" /> 나가기
                </Button>
              </div>

              <div ref={scrollRef} className="flex-1 overflow-y-auto p-6 space-y-4 bg-[#f9fafb] custom-scrollbar">
                {isLoadingHistory && <div className="flex justify-center p-4"><Loader2 className="h-5 w-5 animate-spin text-primary" /></div>}
                {messages.map((msg, idx) => (
                  <div key={idx}>
                    {msg.isSystem ? (
                      <div className="flex justify-center my-4">
                        <span className="px-3 py-1 bg-gray-200 rounded-full text-[10px] text-gray-500 font-bold">{msg.content}</span>
                      </div>
                    ) : (
                      <div className={`flex flex-col ${msg.senderId === userId ? "items-end" : "items-start"}`}>
                        <span className="text-[10px] text-gray-400 mb-1 ml-1 font-bold">{msg.senderName}</span>
                        <div className={`p-3 text-sm shadow-sm max-w-[70%] ${
                          msg.senderId === userId ? "bg-blue-600 text-white rounded-2xl rounded-tr-none" : "bg-white border rounded-2xl rounded-tl-none"
                        }`}>
                          {msg.content}
                        </div>
                      </div>
                    )}
                  </div>
                ))}
              </div>

              <div className="p-4 border-t bg-card/50 backdrop-blur-md shrink-0">
                <form onSubmit={handleSendMessage} className="flex gap-2 max-w-4xl mx-auto">
                  <Input value={inputValue} onChange={(e) => setInputValue(e.target.value)} placeholder="메시지 입력..." className="h-11" />
                  <Button type="submit" size="icon" className="h-11 w-11" disabled={!inputValue.trim()}>
                    <Send className="h-5 w-5" />
                  </Button>
                </form>
              </div>
            </>
          ) : (
            <div className="flex-1 flex flex-col items-center justify-center p-8 text-center h-full text-gray-300">
              <MessageSquare className="h-12 w-12 mb-2 opacity-20" />
              <h3 className="text-lg font-bold">채팅방을 선택하세요</h3>
            </div>
          )}
        </main>
      </div>

      <style jsx global>{`
        .custom-scrollbar::-webkit-scrollbar { width: 4px; }
        .custom-scrollbar::-webkit-scrollbar-thumb { background: #e2e8f0; border-radius: 10px; }
      `}</style>
    </div>
  )
}